import "file-loader?name=[name].[ext]!../../index.html"; //is this filepath correct?
import "../assets/img/rigo-baby.jpg";
import "../assets/img/4geeks.ico";
//import 'breathecode-dom'; //DOM override to make JS easier to use
import "../style/index.scss";

window.onload = () => {
  document.querySelector(".card").classList.add(generateRandomSuit());
  document.querySelector(".card").innerHTML = generateRandomGreeting();
};

let generateRandomGreeting = () => {
  let greetings = [
    "Happy father's day!",
    "Feliz día del padre!",
    "Feliz aniversário Papai!",
    "Joyeuse fête des Pères!",
    "Buona festa del papà!",
    "해피 아버지 날!",
    "Bòn fèt papa!",
    "Glad fars dag!",
    "สุขสันต์วันพ่อ!",
    "Siku ya baba ya furaha!",
    "Selamat Hari Ayah!",
    "Maligayang Araw ng mga tatay!",
    "Latha athair sona!"
  ];
  let indexGreetings = Math.floor(Math.random() * greetings.length);
  return greetings[indexGreetings];
};

let generateRandomSuit = () => {
  let suit = ["diamond", "heart", "club", "spade"];
  let indexSuit = Math.floor(Math.random() * suit.length);
  return suit[indexSuit];
};
